	mounted(){
		wx.getStorage({
			key:'isLogin',
			success(res) {
				console.log(res.data)
			},
			fail(err) {
				console.log('未登录')
				wx.reLaunch({
					url:'../Login/login'
				})
			}
		})
		console.log(this.$mp.query.userInfo)
		if(this.$mp.query.userInfo){
			let userInfo = JSON.parse(this.$mp.query.userInfo)
			this.userInfo = userInfo
		}else {
			wx.getUserInfo({
				success:(res) => {
					this.userInfo = res.userInfo
				},
				fail: () => {
					console.log('获取失败')
				}
			})
		}
	},
	methods: {
		toLogin(){
			if(this.userInfo.nickName){
				return
			}
			wx.navigateTo({
				url: '/pages/login/login'
			})
		},
		logout(){
			wx.clearStorage({
			key:'isLogin',
			success(res) {
				console.log('清除缓存')
				wx.reLaunch({
					url:'../Login/login'
				})
			},
			fail(err) {
